body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
button {
  margin:20px 20px 20px 0px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2{
  display: block;
  text-align: center;

}

span {
  display: inline-block;
  margin-right:15px;
}

.card {
 
  width: 75px;
  height: 75px;
  margin: 25px;
}

.wrapper {
 
  margin: 150px auto;
  max-width: 700px;

  padding-bottom: 15px;
}
.menu {
  margin: auto;
  text-align: center;
  padding-bottom: 12px;
}

.cardWrapper {
  text-align: center;
  margin: auto;
}
.gameBoard
{
  
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  background-color: #dcdcdc;
}

label{
  margin-right:12px;

}
div.inputBox {
  text-align: center;
}